@import "styles/variables";

.search {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  &__icon {
    cursor: pointer;

    position: relative;

    width: 36px;
    height: 36px;
    margin-left: 8px;

    background: $red;
    border: 0;
    border-radius: 7px;
    outline: none;
  }

  img {
    content: "";

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    margin: auto;
  }
}

.multi-dropdown-wrapper {
  width: 50%;
  margin-top: 16px;
  margin-right: 0;
  margin-left: auto;
}

.result {
  font-family: $avenir-font;
  font-size: 18px;
  font-weight: 300;
}
